@import url(https://fonts.googleapis.com/css2?family=MedievalSharp&display=swap);
.frontNews{
    padding: 1rem 1rem 2rem 1rem;
    background: #000000; 
    box-shadow: 0px 0px 1rem 0.5rem #000000; 
    color: #c2903d;
    font-family: 'MedievalSharp', cursive;

}

.frontNews__title{
    text-align: center;
    font-size: 2rem;
    margin-bottom: 2rem;
}

.frontNews__list{
    height: 35rem;
    overflow-y: auto;
}

.frontNews__singleNews{
    margin-bottom: 3rem;
}

.frontNews__singleNews_title{
    margin-left: 1rem;
    margin-bottom: 1rem;
}

.frontNews__singleNews_text{
    text-align: justify;
}

.frontNews__singleNews_description{
    margin-top: 0.5rem;
    font-size: 0.9rem;
    float: right;
    color: #47362e;
}


@media screen and (min-width: 64rem ) {
    .frontNews{
        left: 2rem;
        margin: 0;
    }

    .frontNews__singleNews{
        padding-right: 2rem;
        margin-bottom: 3rem;
    }
}




.frontInfo{
    padding: 1rem;
    background: #000000; 
    box-shadow: 0px 0px 1rem 0.5rem #000000; 
    margin-bottom: 3rem;
}

@media screen and (min-width: 64rem ) {
    .frontInfo{
        left: 2rem;
    }
}

.frontInfo__text{
    color: #c2903d;
}
.header{
    margin: auto;
    width: 100vw;
    max-width: 62.5rem;
    position: relative; 
    box-shadow: 0px 0px 1rem 0.5rem #000000; 
}

@media screen and (min-width: 64rem ) {
    .header{
    margin: 1rem auto 0  auto;
    }
}


.header__pic{
    width: 95vw;
    max-width: 62rem;
}

.header__title{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}


.login{
    display: block;
    margin: 2rem auto 0 auto;
    width: 18.75rem;
    position: relative;
    text-align: center;
}

.login__picBg{ 
    box-shadow: 0px 0px 1rem 0.5rem #000000; 
}

.login__formBox{
    display: block;
    position: absolute;
    top: 2rem;
    width: 18.75rem;
    margin: 0.5rem auto;
}

.login__form{
    margin-bottom: 1rem;
}


@media screen and (min-width: 62.5rem ) {
    .login{
        margin: 2rem 0.2rem 0 auto;

    }
}



.login__btn{
    width: 10rem;
    height: 2rem;
    border-radius: 0;
    background: rgba(255, 255, 255, 0.05);
    border: 4px solid #272524;
    color: #c2903d;
    font-family: 'MedievalSharp', cursive;
    cursor: pointer;
    transition: 300ms;
    margin-bottom: 1rem;
}

.login__btn:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 300ms;
    background: rgba(255, 255, 255, 0.15);
    color: #d48402;
}

.login__btn-notValid{
    opacity: 0.6;
    cursor: not-allowed;
    margin-bottom: 1rem;
}
.login__btn-notValid:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: 300ms;
    background: rgba(255, 255, 255, 0.15);
    color: #c2903d;
}
.login__input{
    width: 12rem;
    padding: 0 1rem;
    height: 2rem;
    text-align: center;
    background: rgba(0, 0, 0, 0.3);
    color: #c2903d;
    border: 2px #c2903d solid;
    margin: 0.2rem auto 1rem auto;
    display: block;
    border-radius: 0.5rem;
}

.login__input:focus{
    -webkit-filter: brightness(130%);
            filter: brightness(130%);
    outline: 0;
}

.login__input-invalid{ 
    box-shadow: 0px 0px 1rem 0.3rem #ff0000; 
}

.login__label{
    margin-top: 1rem;
    color: #47362e;
    font-family: 'MedievalSharp', cursive;
    display: block;
    font-weight: 900;
    text-shadow: -1px -1px 0 #c2903d, 1px -1px 0 #c2903d, -1px 1px 0 #c2903d, 1px 1px 0 #c2903d;
}


.reg__btn{
    width: 10rem;
    height: 2rem;
    border-radius: 0;
    background: rgba(255, 255, 255, 0.05);
    border: 4px solid #272524;
