.reg__btn-big{
    height: 6rem;
    display: block;
    margin: auto;
    width: 12rem;
    font-size: 1.5rem;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 50% 50% 50% 50% / 50% 50% 50% 50% ;
    border: 12px solid rgba(255, 255, 255, 0.05);

}

.reg__btn-big:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 300ms;
    background: rgba(255, 255, 255, 0.05); 
    box-shadow: 0px 0px 1.2rem 0.1rem #dad6ab; 
    color: #c2903d;
}

.reg__btn-exit{
    height: 3rem;
    width: 3rem;
    border-radius: 3rem;
    position: absolute;
    top: -1.5rem;
    right: -1.5rem;
    background: rgba(49, 49, 49, 0.7);
}

.reg__btn-pic{
    position: relative;
    display: block;
    margin: auto;
    width: 16rem;
    background: none;
    border: 0;
    transition: 300ms;
}

.reg__btn-pic:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition: 300ms;
}

.reg__btn-pic img{
    position: absolute;
    top: 0;
    left: 0;
    width: 16rem;
    -webkit-filter: brightness(70%);
            filter: brightness(70%);
}

.reg__btn-pic span{
    position: relative;
    top: 1.5rem;
    font-size: 1.5rem;
    z-index: 2;
    color: #c2903d;
    font-family: 'MedievalSharp', cursive;
}
